import React from 'react'
import request from "../request/api.js"
import { addFavorite, deleFavorite } from '../request/favorite.js'
// import {addFavorite,deleFavorite} from "../request/favorite.js"
import Cookie from "js-cookie"
class Home extends React.Component {
 constructor(props){
   super(props)
   this.state = {
     data:{
       articles:[]
     },
     activepage:1,
     able:1,
     pages:[],
     favoritedisabled:false
   }
   this.fnFavorite = this.fnFavorite.bind(this)
 }
 //点赞
 async fnFavorite(data){

  await this.setState({   //异步
    favoritedisabled:true
  })
  console.log(data)  //单个文章对象
  // data.favorited true 已经点赞 false 没有点赞
  if(data.favorited){  //已经点赞
  // 取消点赞
  await deleFavorite(data).then(res=>{console.log("取消")}) //异步
  data.favorited = false
  data.favoritesCount+=-1

  let key = this.state.data.articles.indexOf(data)
  this.state.data.articles[key] = data
  await this.setState({
    data:{
      articles:this.state.data.articles
    }
  })
  
  }else{
  // 点赞
  await addFavorite(data).then(res=>{console.log("添加")})  //异步
  data.favorited = true
  data.favoritesCount+=1
 
  let key = this.state.data.articles.indexOf(data)
  this.state.data.articles[key] = data

  await this.setState({
    data:{
      articles:this.state.data.articles
    }
  })
  }

  await this.setState({   //异步
    favoritedisabled:false
  })
 }

 componentDidMount(){
  
  request({
    method:"GET",
    url:"/api/articles",
    params:{

    }
  }).then(res=>{
    
    var pages = [];
    res.data.articles.forEach((item,index)=>{
      item.favoritedisabled = this.state.favoritedisabled
    })
    for(var i=0;i<res.data.articlesCount/20;i++){
      pages.push(i);
    };
    console.log(res)
    this.setState({
      data:res.data,
      pages:pages

    })
  })


 }
 render(){
   return (
    <div className="home-page">

    <div className="banner">
      <div className="container">
        <h1 className="logo-font">conduit</h1>
        <p>A place to share your knowledge.</p>
      </div>
    </div>
  
    <div className="container page">
      <div className="row">
  
        <div className="col-md-9">
          <div className="feed-toggle">
            <ul className="nav nav-pills outline-active">
              <li className="nav-item">
                <a className="nav-link disabled" href="">Your Feed</a>
              </li>
              <li className="nav-item">
   <a className="nav-link active" href="">Global Feed</a>
              </li>
            </ul>
          </div>
          
          {
            this.state.data.articles.map((item,index)=>{
              return (
                <div className="article-preview" key = {index}>
                  <div className="article-meta">
                    <a href="profile.html"><img src={item.author.image} /></a>
                    <div className="info">
              <a href="" className="author">{item.author.username}</a>
              <span className="date">{item.createdAt}</span>
                    </div>
                    <button className="btn btn-outline-primary btn-sm pull-xs-right" onClick={()=>{
                      this.fnFavorite(item)
                    }} disabled = {this.state.favoritedisabled}>
                      <i className="ion-heart"></i> {item.favoritesCount}
                    </button>
                  </div>
                  <a href="" className="preview-link">
              <h1>{item.title}</h1>
              <p>{item.description}</p>
                    <span>Read more...</span>
                  </a>
                </div>
              )
            })
          }

<ul className="pagination">
{
  this.state.pages.map((item,key)=>{
    return (
      <li className={this.state.activepage == key+1?"page-item active":"page-item"} key = {key}>
        <a className="page-link" href="">{key+1}</a>
      </li>
    )
  })
}
</ul>
      
          
  
        </div>
  
        <div className="col-md-3">
          <div className="sidebar">
            <p>Popular Tags</p>
  
            <div className="tag-list">
              <a href="" className="tag-pill tag-default">programming</a>
              <a href="" className="tag-pill tag-default">javascript</a>
              <a href="" className="tag-pill tag-default">emberjs</a>
              <a href="" className="tag-pill tag-default">angularjs</a>
              <a href="" className="tag-pill tag-default">react</a>
              <a href="" className="tag-pill tag-default">mean</a>
              <a href="" className="tag-pill tag-default">node</a>
              <a href="" className="tag-pill tag-default">rails</a>
            </div>
          </div>
        </div>
  
      </div>
    </div>
  
  </div>
   )
 }
}

export default Home