import React from "react"
import request from "../request/api.js"
import { Redirect,Switch } from "react-router-dom";
import Cookie from "js-cookie"
import Pubsub from "pubsub-js"



class A extends React.Component{
    constructor(props){
        super(props)
        this.blur = this.blur.bind(this)
    }
    blur(ev){
       var value = ev.target.value
       console.log(value)
       Pubsub.publish("value",value)
    }
    render(){
        return (
            <fieldset className="form-group">
                <input className="form-control form-control-lg" type="text" placeholder="Your Name" onBlur = {this.blur}/>
            </fieldset>
        )
    }
}
class Login extends React.Component{
    constructor(props){
        super(props)
        this.onsubmit = this.onsubmit.bind(this)
        this.onblur1 = this.onblur1.bind(this)
        this.onblur2 = this.onblur2.bind(this)
        //将input中的数据实现成响应式
        this.state = {
            username:"",
            email:"11111",
            password:"11111"
        }
    }
    componentDidMount(){
    // 生命周期函数
      
        Pubsub.subscribe("value",(msg,data)=>{
            this.setState({
                username:data
            })
        })

    }
    islogin(){
        //login  登录  register 注册
        return this.props.match.path === "/login"
    }
    onsubmit(ev){
        //取消默认行为
        ev.preventDefault()
        let user = {
            username:this.state.username?this.state.username:"",
            email:this.state.email,
            password:this.state.password
        }
        console.log(user)
        request({
            method:"POST",
            url:this.state.username?"/api/users":"/api/users/login",
            data:{
                user:user
            }
        }).then(res=>{
            console.log(this)
            console.log(res)
            //将token存放在cookie中
            Cookie.set("auth",res.data.user.token)
            this.props.history.push("/")
            // 发布
            Pubsub.publish("auth",res.data.user.token)

        })
        
        // let user = {
        //     username:this.state.username?this.state.username:"",
        //     email:this.state.email,
        //     password:this.state.password
        // }
        // // console.log(user)
        // request({
        //     method:"POST",
        //     url:this.state.username?"/api/users":"/api/users/login",
        //     data:{
        //         user:user
        //     }
        // }).then(res=>{
        //     // console.log(this)
        //     console.log(res)
        //     Cookie.set("auth",res.data.user.token)
        //     PubSub.publish("auth",res.data.user.token)
        //     this.props.history.push("/")
            
        // })
       
    }
    onblur1(ev){
        // console.log(ev.target.value)
        this.setState({
            email:ev.target.value
        })
    }
    onblur2(ev){
        // console.log(ev.target.value)
        this.setState({
            password:ev.target.value
        })
    }
    render(){
        // console.log(this.state)
        return (
            <div className="auth-page">
                <div className="container page">
                    <div className="row">
                    <div className="col-md-6 offset-md-3 col-xs-12">
                        <h1 className="text-xs-center">
                            {this.islogin()?'Sign in':"Sign up"}
                        </h1>
                        <p className="text-xs-center">
                        <a href="">
                        {this.islogin()?'Need an account?':"Have an account?"}
                            
                            
                        </a>
                        </p>

                        <ul className="error-messages">
                        <li>That email is already taken</li>
                        </ul>

                        <form onSubmit = {this.onsubmit}>
                        {this.islogin()?"":<A/>}
                        <fieldset className="form-group">
                            <input className="form-control form-control-lg"  type="text" placeholder="Email" onBlur = {this.onblur1}/>
                        </fieldset>
                        <fieldset className="form-group">
                            
                            <input className="form-control form-control-lg"  type="password" placeholder="Password" onBlur = {this.onblur2}/>
                        </fieldset>
                        <button className="btn btn-lg btn-primary pull-xs-right">
                        {this.islogin()?'Sign in':"Sign up"}
                            
                        </button>
                        </form>
                    </div>

                    </div>
                </div>
                </div>
        )

    }
}

export default Login