import React from 'react';
import { Link,NavLink } from "react-router-dom";
import Cookie from "js-cookie"
import Pubsub from "pubsub-js"

class Heads extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      auth:""
    }
  }
  componentDidMount(){
    var that = this
    Pubsub.subscribe("auth",function(msg,data){
      console.log(msg,data)
      //将cookie中的auth 和 state中的auth 关联
      that.setState({
        auth:data
      })
    })

  }
  componentWillUpdate(){
    
  }
  render(){
      //cookie中auth存在
      if(Cookie.get("auth")){
        return (
          <div>
            <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">conduit</a>
                <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                   
                    <NavLink className="nav-link" to="/">Home</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link" to="/editor" activeClassName="active">
                    <i className="ion-compose"></i>&nbsp;New Post
                    </NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link" to="/setting" activeClassName="active">
                    <i className="ion-gear-a"></i>&nbsp;Settings
                    </NavLink>
                </li>
    
                <li className="nav-item">
                    <NavLink className="nav-link ng-binding" to="/profile" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })"  activeClassName="active">
                      <img ng-src="https://s1.ax1x.com/2020/10/17/0Omzan.th.jpg" className="user-pic" src="https://s1.ax1x.com/2020/10/17/0Omzan.th.jpg"/>
                      cwj 
                    </NavLink>
                </li>
                </ul>
            </div>
            </nav>
          </div>
        )
      }else{
        return (
          <div>
            <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">conduit</a>
                <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                   
                    <NavLink className="nav-link" to="/">Home</NavLink>
                </li>
                
                <li className="nav-item">
                    <NavLink className="nav-link" to="/login" activeClassName="active">Sign in</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link" to="/register" activeClassName="active">Sign up</NavLink>
                </li>
    
               
                </ul>
            </div>
            </nav>
          </div>
        )
      }
     
    }

  
}



export default Heads;
